/**  **/

const _array = {

  /**
   * 
   * @param array
   * @param item
   * @returns {*}
   */
   update: (array, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    for (let i = 0; i < array.length; i++) {
      let value = array[i]
      if (item === value) {
        array.splice(i, 1)
      }
    }
    array.push(item)
    return array
  },

  /**
   * 
   * @param array
   * @param item
   * @returns {*}
   */
   add: (array, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    for(let i = 0; i < array.length; i++) {
      let value = array[i]
      if (item === value) {
        return array
      }
    }
    array.push(item)
    return array
  },

  /**
   * 
   * @param array
   * @param item
   * @returns {*}
   */
   push: (array, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.push(item)
    return array
  },

  /**
   * 
   * @param array
   * @param item
   * @returns {*}
   */
   unshift: (array, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.unshift(item)
    return array
  },

  /**
   * 
   * @param array
   * @param location
   * @param item
   * @returns {*}
   * @private
   */
  _splice: (array, location, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.splice(location, 0, item)
    return array
  },

  /**
   * 
   * @param array
   * @param item
   * @param location
   * @returns {*}
   */
   splice_A: (array, location, length, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.splice(location, length, item)
    return array
  },

  /**
   * 
   * @param array
   * @param item
   * @returns {*}
   */
   remove: (array, item) => {
    if(!Array.isArray(array)) {
      return []
    }
    for(let i = 0; i < array.length; i++) {
      let value = array[i]
      if (item === value) {
        array.splice(i, 1)
      }
    }
    return array
  },

  /**
   * 
   * @param array
   * @returns {*}
   */
   pop: (array) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.pop()
    return array
  },

  /**
   * 
   * @param array
   * @returns {*}
   */
   shift: (array) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.shift()
    return array
  },

  /**
   * 
   * @param array
   * @param location
   * @param length
   * @returns {*}
   */
   splice_D: (array, location, length) => {
    if(!Array.isArray(array)) {
      return []
    }
    array.splice(location, length)
    return array
  },

  /**
   * 
   * @param array
   * @param index1
   * @param index2
   * @returns {*}
   */
  swapItems: (array, index1, index2) => {
    array[index1] = array.splice(index2, 1, array[index1])[0];
    return array;
  },

  /**
   * 
   * @param array
   * @param index
   * @returns {*}
   */
  moveItemToFirst: (array, index) => {
    var temp = array[index];
    if(index == 0) return array;
    for (var i = 0; i < array.length; i++) {
      if(array[i] === array[index]) {
        array.splice(i, 1);
        break;
      }
    }
    array.unshift(temp);
    return array;
  },

  /**
   * 
   * @param arr1
   * @param arr2
   * @returns {boolean}
   */
   isEqual: (array1, array2) => {
    if(!(Array.isArray(array1) && Array.isArray(array2))) {
      return false
    }
    if(array1.length !== array2.length) {
      return false
    }
    for(let i = 0; i < array1.length; i++) {
      if (array1[i] !== array2[i]) {
        return false
      }
    }
    return true
  }
}

export {_array}


