import {getStorage, setStorage} from '@/utils/wxapi'

/**
 * @param keykey url
 * @param fetchFunc
 * @param isCache
 * @returns {value}
 */
const dataCache = (key, fetchFunc, isCache) => {
  // 
  if (!isCache) {
    return fetchFunc()
  }
  // 
  return getStorage(key).then(value => {
    if (value) {
      // 
      return value
    } else {
      // 
      return fetchFunc().then(value => {
        value ? setStorage(key, value) : null
        return value
      })
    }
  })
}

export {dataCache}


